@import '../../common/generic';

.neighbours-view {
    @include flex(center, center);
    grid-gap: 10px;
    .thumbnail {
        @include img-size(65px);
        background-color: $img-placeholder;

        background-size: contain;
        cursor: pointer; }
    .active {
        outline: 1px solid #999;
        outline-offset: 4px;
        pointer-events: none; } }
